import { FormEvent, useState } from 'react'
import Avatar from '@mui/material/Avatar'
import Button from '@mui/material/Button'
import CssBaseline from '@mui/material/CssBaseline'
import TextField from '@mui/material/TextField'
import Grid from '@mui/material/Grid'
import Box from '@mui/material/Box'
import LockOutlinedIcon from '@mui/icons-material/LockOutlined'
import Typography from '@mui/material/Typography'
import Container from '@mui/material/Container'
import { createTheme, ThemeProvider } from '@mui/material/styles'
import styles from './LoginPage.module.scss'
import classnames from 'classnames/bind'
import call from '../../utility/utility'
import { StatusCodes } from 'http-status-codes'
import crypto from 'crypto-js'
import { useNavigate, Link } from 'react-router-dom'
const cx = classnames.bind(styles)

const theme = createTheme()

export default function SignIn() {
	const movePage = useNavigate()
	const [idError, setIdError] = useState(false)
	const [passwordError, setPasswordError] = useState(false)
	const [idErrorMessage, setIdErrorMessage] = useState('')
	const [passwordErrorMessage, setPasswordErrorMessage] = useState('')
	const [loginError, setLoginError] = useState(false)

	const handleSubmit = (event: FormEvent<HTMLFormElement>) => {
		event.preventDefault()
		const data = new FormData(event.currentTarget)
		const id = data.get('id')
		const password = data.get('password')
		let valid = true
		setLoginError(false)
		setIdError(false)
		setPasswordError(false)
		if (!id) {
			setIdError(true)
			setIdErrorMessage(' !')
			valid = false
		}
		if (!password) {
			setPasswordError(true)
			setPasswordErrorMessage(' !')
			valid = false
		}
		if (valid) {
			const pw = crypto.SHA512(password!.toString()).toString()
			const body = { id, password: pw }
			call('/login', 'POST', undefined, body, true, movePage).then(res => {
				if (res.status === StatusCodes.UNAUTHORIZED) {
					setIdError(true)
					setPasswordError(true)
					setIdErrorMessage('')
					setPasswordErrorMessage('')
					setLoginError(true)
					return
				}
				if (res.status === StatusCodes.BAD_REQUEST) {
					alert('   .')
					return
				}
				movePage('/')
			})
		}
	}

	return (
		<ThemeProvider theme={theme}>
			<Container component="main" maxWidth="xs">
				<CssBaseline />
				<Box
					sx={{
						marginTop: 8,
						display: 'flex',
						flexDirection: 'column',
						alignItems: 'center',
					}}
				>
					<Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
						<LockOutlinedIcon />
					</Avatar>
					<Typography component="h1" variant="h5">
						
					</Typography>
					<Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
						<TextField error={idError} margin="normal" required fullWidth id="id" label="" name="id" autoFocus />
						{idError && <span className={cx('error')}>{idErrorMessage}</span>}
						<TextField error={passwordError} margin="normal" required fullWidth name="password" label="" type="password" id="password" />
						{passwordError && <span className={cx('error')}>{passwordErrorMessage}</span>}
						<br />
						{loginError && <span className={cx('error')}>   .</span>}
						<br />
						<Button type="submit" fullWidth variant="contained" sx={{ mt: 3, mb: 2 }}>
							
						</Button>
						<Grid container justifyContent="flex-end">
							<Grid item>
								<Link to="/register" className={cx('link')}>
									 ?
								</Link>
							</Grid>
						</Grid>
					</Box>
				</Box>
			</Container>
		</ThemeProvider>
	)
}
