import { FormEvent, useState } from 'react'
import Avatar from '@mui/material/Avatar'
import Button from '@mui/material/Button'
import CssBaseline from '@mui/material/CssBaseline'
import TextField from '@mui/material/TextField'
import Grid from '@mui/material/Grid'
import Box from '@mui/material/Box'
import LockOutlinedIcon from '@mui/icons-material/LockOutlined'
import Typography from '@mui/material/Typography'
import Container from '@mui/material/Container'
import { createTheme, ThemeProvider } from '@mui/material/styles'
import styles from './RegisterPage.module.scss'
import classnames from 'classnames/bind'
import call from '../../utility/utility'
import { StatusCodes } from 'http-status-codes'
import crypto from 'crypto-js'
import { passwordRegex } from '../../_config/config'
import { useNavigate, Link } from 'react-router-dom'
const cx = classnames.bind(styles)

const theme = createTheme()

export default function SignUp() {
	const movePage = useNavigate()
	const [idError, setIdError] = useState(false)
	const [passwordError, setPasswordError] = useState(false)
	const [passwordCheckError, setPasswordCheckError] = useState(false)
	const [idErrorMessage, setIdErrorMessage] = useState('')
	const [passwordErrorMessage, setPasswordErrorMessage] = useState('')
	const [passwordCheckErrorMessage, setPasswordCheckErrorMessage] = useState('')

	const handleSubmit = (event: FormEvent<HTMLFormElement>) => {
		event.preventDefault()
		const data = new FormData(event.currentTarget)
		const id = data.get('id')
		const password = data.get('password')
		const passwordCheck = data.get('passwordCheck')
		let valid = true

		setIdError(false)
		setPasswordError(false)
		setPasswordCheckError(false)
		if (!id) {
			setIdError(true)
			setIdErrorMessage(' !')
			valid = false
		}
		if (!password) {
			setPasswordError(true)
			setPasswordErrorMessage(' !')
			valid = false
		} else if (password.toString().match(passwordRegex) === null) {
			setPasswordError(true)
			setPasswordErrorMessage('     8  !')
			valid = false
		}
		if (!passwordCheck) {
			setPasswordCheckError(true)
			setPasswordCheckErrorMessage('  !')
			valid = false
		} else if (password !== passwordCheck) {
			setPasswordCheckError(true)
			setPasswordCheckErrorMessage('  !')
			valid = false
		}

		if (valid) {
			const pw = crypto.SHA512(password!.toString()).toString()
			const body = { id, password: pw }
			call('/register', 'POST', undefined, body, true).then(res => {
				if (res.status === StatusCodes.BAD_REQUEST) {
					setIdError(true)
					setIdErrorMessage('  !')
					return
				}
				alert('   .       .')
				movePage('/login')
			})
		}
	}

	return (
		<ThemeProvider theme={theme}>
			<Container component="main" maxWidth="xs">
				<CssBaseline />
				<Box
					sx={{
						marginTop: 8,
						display: 'flex',
						flexDirection: 'column',
						alignItems: 'center',
					}}
				>
					<Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
						<LockOutlinedIcon />
					</Avatar>
					<Typography component="h1" variant="h5">
						
					</Typography>
					<Box component="form" noValidate onSubmit={handleSubmit} sx={{ mt: 3 }}>
						<Grid container spacing={2}>
							<Grid item xs={12}>
								<TextField error={idError} required fullWidth id="email" label="" name="id" />
								{idError && <span className={cx('error')}>{idErrorMessage}</span>}
							</Grid>
							<Grid item xs={12}>
								<TextField error={passwordError} required fullWidth name="password" label="" type="password" id="password" />
								{passwordError && <span className={cx('error')}>{passwordErrorMessage}</span>}
							</Grid>
							<Grid item xs={12}>
								<TextField error={passwordCheckError} required fullWidth name="passwordCheck" label=" " type="password" id="passwordCheck" />
								{passwordCheckError && <span className={cx('error')}>{passwordCheckErrorMessage}</span>}
							</Grid>
						</Grid>
						<Button type="submit" fullWidth variant="contained" sx={{ mt: 3, mb: 2 }}>
							
						</Button>
						<Grid container justifyContent="flex-end">
							<Grid item>
								<Link to="/login" className={ cx('link')}>
									  ?
								</Link>
							</Grid>
						</Grid>
					</Box>
				</Box>
			</Container>
		</ThemeProvider>
	)
}
